<template>
  <div class="Chart">
    <div class="Chart-left d-flex flex-column ">
        <div class="add">
          <h4 class="add_title">{{title}}</h4>
          <div class="row">
            <div class="col-xl-6 col-12">
                <p class="item-name"></p>
                <el-input  type="text"  placeholder=""  v-model="itemName"  maxlength="10"  show-word-limit></el-input>
            </div>
             <div class="col-xl-6 col-12">
                <p class="item-name"></p>
                <el-input  type="text"  placeholder=""  v-model="itemAmount"></el-input>
             </div>
          </div>
          <div class="row mt-1">
            <div class="col-xl-6 col-12">
                <p class="item-name"></p>
                  <el-select v-model="itemCategory" placeholder="" style="width:100%" >
                  <el-option v-for="(item,index) in categoryOption" :key="index" :label="item" :value="item"></el-option>
                </el-select>
            </div>
             <div class="col-xl-6 col-12">
                <p class="item-name"></p>
                 <el-date-picker type="date" placeholder="" v-model="itemDate" style="width: 100%;" value-format="yyyy-MM-dd"></el-date-picker>
             </div>
          </div>
          <div class="row add_btn">
            <div class="col-auto">
            <el-button type="primary" @click.native="add()" v-if="title===''"></el-button>
            <el-button type="primary" @click.native="edit()" v-else></el-button>
            </div>
           
          </div>
        </div>
        <div class="item">
          <el-table
          :data="tableData"
          style="width: 99.9%"
          height="280"
          >
           <el-table-column
            type="index"
            width="50">
          </el-table-column>
          <el-table-column
            prop="date"
            label=""
            align="left"
            width="120"
            >
          </el-table-column>
          <el-table-column
            prop="name"
            label=""
            align="left"
            width="100"
            >
          </el-table-column>
          <el-table-column
            prop="category"
            label=""
            align="left"
            width="80"
            >
          </el-table-column>
          <el-table-column
            prop="amount"
            label=""
            align="center"
            width="60"
            >
          </el-table-column>
          <el-table-column label="" align="center" width="120">
            <template slot-scope="scope">
              <el-button 
                size="mini"
                @click="handleEdit(scope.$index, scope.row)"><i class="el-icon-edit"></i></el-button>               
              <el-button
                size="mini"
                type="danger"
                @click="DeleteTmp(scope.$index, scope.row)"
              ><i class="el-icon-delete"></i></el-button>
            </template>
          </el-table-column>
          </el-table>
          <el-dialog
              title=""
              :visible.sync="dialogVisible"
              width="30%">
              <span></span>
              <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false"> </el-button>
                <el-button type="primary" @click="handleDelete()"> </el-button>
              </span>
          </el-dialog>
        </div>
    </div>
    <div class="Chart-right d-flex flex-column">
        <div class="Line">
          <LineChart class="lineimg" :categoryOption="categoryOption" :tableData="tableData" ref="lineImg"/>
        </div>
        <div class="Pie">
          <PieChart class="pieimg" :categoryOption="categoryOption" :tableData="tableData"  ref="pieImg"/>
        </div>
    </div>
  </div>
</template>
<script>
import PieChart from "../components/chart/Pie.vue";
import LineChart from '../components/chart/Line.vue';

export default {
    name:"Chart",
    data() {
      return {
          tableData: [
            {date: '2016-05-02',name: '',category:'',amount:30}, 
            {date: '2016-05-04',name: '',category:'',amount:50}, 
            {date: '2016-05-01',name: '',category:'',amount:25},
            {date: '2016-05-03',name: '',category:'',amount:30},
            {date: '2016-05-03',name: '',category:'',amount:45},
            {date: '2016-05-03',name: '95',category:'',amount:55}
          ],  
          categoryOption:['','','','',''],
          itemName:'',
          itemAmount:0,
          itemCategory:'',
          itemDate:'',
          dialogVisible:false,
          tmpRowid:'',
          title:''
      }
    },
    methods:{
      handleEdit(e){
        this.itemName=this.tableData[e].name;
        this.itemDate=this.tableData[e].date;
        this.itemCategory=this.tableData[e].category;
        this.itemAmount=this.tableData[e].amount;
        this.tmpRowid=e;
        this.title='';
      },
       handleDelete() {
        let rowId=this.tmpRowid ;
        this.tableData.splice(rowId,1);
        this.dialogVisible=false;
        this.tmpRowid='';
      },
      add(){
        let data ={date:this.itemDate,name: this.itemName,category:this.itemCategory,amount:Number(this.itemAmount)};
        this.tableData.push(data);
        this.itemName='';
        this.itemDate='';
        this.itemCategory='';
        this.itemAmount=0;
      },
      DeleteTmp(b){
        this.dialogVisible=true;      
        this.tmpRowid=b;
      },
      edit(){
        let rowId=this.tmpRowid ;
        this.tableData[rowId].name=this.itemName;
        this.tableData[rowId].date=this.itemDate;
        this.tableData[rowId].category=this.itemCategory;
        this.tableData[rowId].amount=Number(this.itemAmount);
        this.tmpRowid='';
        this.title='';
        this.itemName='';
        this.itemDate='';
        this.itemCategory='';
        this.itemAmount=0;
        this.$refs.lineImg.updateData();
        this.$refs.pieImg.updateData();
      }
    },
    components:{
      PieChart,
      LineChart
    }
}
</script>
<style scoped>
.Chart{
  background-color:		#F5F5F5;
  min-height: 93vh;
  display: flex;
}
.Chart-left{
  padding: 10px;
  margin: 5px 0 10px 10px;
  width: 50%;
  border-radius: 4px;
}
.Chart-right{
  padding: 10px ;
  margin: 5px 0 10px 0;
  width: 50%;
  border-radius: 4px;
}
.item{
  padding: 10px 10px 20px 20px;
  border-radius: 4px;
  background-color:white;
  height: 55%;
  margin-top: 2%;
}
.add{
  padding: 20px 10px 20px 20px;
  border-radius: 4px;
  background-color:white;
  height: 45%;
}
.Line{
  padding: 20px 20px 20px 10px;
  border-radius: 4px;
  background-color:white;
  height: 50%;
}
.Pie{
  margin-top: 2%;
  padding: 20px 20px 20px 10px;
  border-radius: 4px;
  background-color:white;
  height: 50%;
}
.add_title{
  text-align: left;
  font-size: 20px;
  margin-top: -2%;
}
.item-name{
  text-align: left;
  font-size: 16px;
  margin-bottom:5px;
}
.form-control{
  border: 1px solid #DCDFE6;
}
.add_btn{
  margin-bottom: -2%;
  margin-top: 3%;
}
.lineimg{
  height: 240px;
  width: auto;
}
.pieimg{
  height: 235px;
  width: auto;
}
/deep/.el-dialog__header{
  padding-top: 30px;
  text-align: left;
}
/deep/.el-dialog__body{
  padding-top: 30px;
  text-align: left;
  font-size: 18px;
  font-weight: normal ;
  color: #606266;
}
/deep/.el-dialog__title{
  line-height: 28px;
  font-size: 22px;
  text-align: left;
  font-weight: normal ;
  color: #303133;
  margin-top: 10%;
}
 /* //  */
  /deep/ .el-table__body-wrapper::-webkit-scrollbar {
    width: 6px; 
    height: 6px; 
  }
  /* //  */
  /deep/ .el-table__body-wrapper::-webkit-scrollbar-thumb {
    background-color: #ddd;
    border-radius: 3px;
  }
   @media  (max-width: 1140px) {
     .Chart{
       flex-direction: column;
     }
     .Chart-left{
       width: 99%;
       margin: 0 0 0 10px;
     }
     .Chart-right{
       width: 99%;
       padding: 0 10px 20px 20px;
        margin: 0;
     }
     .add,.item{
       margin-right: 3%;
     }
     .Line, .Pie{
      width: 99%;
      padding: 10px 10px 20px 20px;
     }
     .action_btn{
       background-color: red;
     }
     .add_title{
        text-align: left;
        font-size: 20px;
        margin-top: 1px;
     }
     .add_btn{
       margin-bottom: 1px;
     }
   }
   @media screen and (max-width: 415px){
     .Chart-left{
       width: 400px;
     }
     .add{
        width: 380px;
        padding-right: 10px;
     }
    .item{
        width: 380px;
        padding-right: 10px;
     }
   }
</style>
